export default {
	data() {
		return {
			img:[],
			article:[],
			cat_id:'',
			pagemax:'',//总的数量
			pagenow:0,//当前的页数
			shownumber:10,//每页的分页条数
			
			contentText: {
				contentdown: "查看更多",
				contentrefresh: "加载中",
				contentnomore: "没有更多"
			},
			status:'loading',
			shouLoading:true,
			haveData:false,
		}
	},
	onLoad(e) {
		var t=this;	
		if(e.cat_id){
			t.cat_id=e.cat_id;
		}
		t.img[0]=t.sta.sitestatic+'img/dl-icon.png';
		//获取新闻列表
		t.getArtList();
	},
	onUnload() {
		this.max = 0,
		this.data = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.shownumber >= this.pagemax) {
			return;
		}
		setTimeout(() => {
			this.setDate();
		}, 100);
	},
	onPullDownRefresh() {
		this.initData();
	},	
	methods: {
		articleDetail:function(id){
			uni.navigateTo({
				url: '../article/article_detail?id='+id
			})
		},
		getArtList:function(){
			uni.showLoading();
			var t=this;
			var data={
				cat_id:t.cat_id
			}
			t.urlRequest.urlRequest(t.func.getArtList,data,function(res){
				t.pagemax=res.data.count;
				for (var i = 0; i < res.data.list.length; i++) {
					if(res.data.list[i]['article_thumb']!=null){
						res.data.list[i]['article_thumb']=t.sta.sitebase+res.data.list[i]['article_thumb'];
					}else{
						res.data.list[i]['article_thumb']='../../static/img/logo.jpg';
					}
					t.article.push(res.data.list[i]);
				}
				uni.hideLoading();
			})
		
		},
		initData(){
			uni.showLoading();
			const t = this;
			var data={
				cat_id:t.cat_id
			}
			t.urlRequest.urlRequest(t.func.getArtList,data,function(res){
				t.pagemax=res.data.count;
				for (var i = 0; i < res.data.list.length; i++) {
					if(res.data.list[i]['article_thumb']!=null){
						res.data.list[i]['article_thumb']=t.sta.sitebase+res.data.list[i]['article_thumb'];
					}else{
						res.data.list[i]['article_thumb']='../../static/img/logo.jpg';
					}
				}
				t.article=res.data.list;
				uni.hideLoading();
				uni.stopPullDownRefresh();
			})
		},
		setDate() {
			uni.showLoading();
			this.shownumber=this.pagenow*10;
			this.pagenow++;
			var t=this;
			
			var data={
				cat_id:t.cat_id,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.getArtList,data,function(res){
				if(res.data.list){
					setTimeout(() => {
						t.haveData =true
					}, 100);
					for (var i = 0; i < res.data.list.length; i++) {
						if(res.data.list[i]['article_thumb']!=null){
							res.data.list[i]['article_thumb']=t.sta.sitebase+res.data.list[i]['article_thumb'];
						}else{
							res.data.list[i]['article_thumb']='../../static/img/logo.jpg';
						}
					}
					t.article = t.article.concat(res.data.list);
					t.pagemax=res.data.count
				}
				uni.hideLoading();
			})
		},
		togglePopup(type) {
			this.type = type;
		},
	}
}
